* {
  color: black;
}

body {
  background-color: #7f8d92;
}

#main {
  display: inline;

  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.difficult {
  width: 100px;
  height: 50px;
  font-size: 25px;
  border-radius: 8px;
  background-color: #7f8d92;
  color: white;
}

#result {
  border: none;
  background-image: none;
  background-color: transparent;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  text-align: center;
  font-size: 40px;
}

.num {
  position: relative;
  left: 60%;
  transform: translate(-50%, -0%);
  margin-bottom: 0px;
  font-size: 40px;
  width: 200px;
}

.num.num1 {
  margin-bottom: -20px;
}

.num.num2 {
  margin-top: -20px;
}

#divisor {
  width: 250px;
}

.enviar {
  margin-top: 25px;
  width: 100px;
  height: 50px;
  font-size: 25px;
  border-radius: 8px;
  background-color: #7f8d92;
  color: white;
}

.zerar {
  margin-top: 25px;
  width: 50px;
  height: 25;
  font-size: 15px;
  border-radius: 8px;
  background-color: #7f8d92;
  color: white;
}
